<template>
    <div>
        <div class="header-wrap">
            <mt-header title="美食">
                <router-link to="/" slot="left">
                    <mt-button icon="back"></mt-button>
                </router-link>
            </mt-header>
            <div class="select">
                <ul class="select-wrap">
                    <li class="select-item" v-for="item in selectlist" :key="item.id"
                    v-bind:class="{ 'active': selectItem == item}"
                    @click="select(item)">
                        {{item.title}}
                    </li>
                </ul>
                <div class="select-click" @click="selectMoreClick()"><i class="fas fa-chevron-down"></i></div>
            </div>
            <div class="select-more" v-if="selectMore">
                <div class="select-more-wrap">
                    <div class="select-more-title">
                        <div>请选择分类</div>
                        <div class="select-more-over" @click="selectMoreOver()">
                            <i class="fas fa-times"></i>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <shop-list :list='indexlist'></shop-list>
    </div>
</template>
<script>
import api from '@/axios/api.js'
import '@/mock.js'
const shopList = () => import('@/components/shop-list')
export default {
  components: {
    shopList
  },
  data: function () {
    return {
      show: false,
      isfixed: false,
      indexlist:[],
      selectlist:[],
      selectItem:[],
      selectMore:false
    }
  },
  created(){
    api.getlist('/indexlist').then(res => {
        this.indexlist = res.list;
        console.log(res.list)
    });
    api.getlist('/selectlist').then(res => {
        this.selectlist = res.list;
        this.selectItem = res.list[0];
        console.log(res.list);
        console.log(this.selectItem);
    });
  },
  mounted() {
    window.onscroll = scroll;
    let a=this;
    function scroll(){
      if (window.scrollY >= 62){
          a.isfixed = true;
      }
      else if(window.scrollY < 62){
          a.isfixed = false;
      }
    }
  },
  methods: {
    select (item) {
        this.selectItem = item;
        api.getlist('/indexlist').then(res => {
        this.indexlist = res.list;
        console.log(res.list)
    });
    },
    selectMoreOver: function () {
        this.selectMore=false;
    },
    selectMoreClick: function () {
        this.selectMore=true;
    }
  }
}
</script>
<style scoped>
.header-wrap{
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    z-index: 2;
}
.shop-list{
    position: relative;
    top: 30vw;
}
.select{
    width: 100vw;
    height: 10vw;
    position: relative;
    background-color: #26a2ff;
    color: rgb(229, 226, 226);
}
.select-wrap{
    width: 90vw;
    height: 100%;
    list-style: none;
    overflow:scroll;
    white-space: nowrap;
    padding-left: 3vw;
}
.select-item{
    display: inline-block;
    margin-right: 8vw;
    height: 8vw;
    font-size: 3.6vw;
    line-height: 8vw;
}
.active{
    color: rgb(255, 255, 255);
    font-weight: 700;
    border-bottom: 2px solid white;
}
.select-click{
    position: absolute;
    top: 0;
    right: 0;
    height: 10vw;
    font-size: 3vw;
    line-height: 10vw;
    width: 10vw;
    padding-right: 3vw;
    text-align: center;
}
.select-more{
    width: 100vw;
    height: 100vh;
    position: fixed;
    top: 0;
    left: 0;
    background-color: rgba(0,0,0,.5);
    z-index: 2;
}
.select-more-wrap{
    height: 100vw;
    background-color: white;
}
/*选择分类*/
.select-more-title{
    position: relative;
    width: 100vw;
    height: 10vw;
    font-size: 3.8vw;
    font-weight: 600;
    padding: 0 2.5vw;
    line-height: 10vw;
    background-color: rgb(241, 243, 243);
}
.select-more-over{
    position: absolute;
    top: 0;
    right: 0;
    width: 5vw;
    height: 5vw;
    margin: 2.5vw;
    text-align: center;
    font-size: 5vw;
    line-height: 5vw;
    color: #666;
}
</style>




